import React from 'react';
import {formatPrice} from '../helpers';
import CSSTransitionGroup from 'react-addons-css-transition-group';

class Order extends React.Component {
	constructor(){
		super()
		this.renderTotal=this.renderTotal.bind(this)
	}
	renderTotal(key){
		const fish =this.props.fishes[key]
		const count =this.props.order[key]
		const button= <button onClick={()=>this.props.removeOrder(key)}>&times;</button>
		if (!fish || fish.status==="unavailable") {
			return <li key={key}>Sorry , {fish ? fish.name : "fish"} is no longer available {button}</li>
		}
		return (
				<li key={key}>
					<span>
						<CSSTransitionGroup
							component="span"
							className="count"
							transitionName="count"
							transitionEnterTimeout={250}
							transitionLeaveTimeout={250}
						>
							<span key={count}>
								{count}
							</span>
						</CSSTransitionGroup>
						lbs {fish.name} {button}
					</span>
					<span>{formatPrice(count * fish.price)}</span>
				</li>
			)
	}
  render() {
  	const orderIds=Object.keys(this.props.order)
  	const total = orderIds.reduce((prevTotal, key)=>{
		const fish =this.props.fishes[key]
		const count =this.props.order[key]
		const isAvailable= fish && fish.status==="available"
		if (isAvailable) {
			return prevTotal + (count * fish.price || 0)
		}
		return isAvailable;
  	},0)
    return (
      <div className="order-wrap">
				<h2>Your Order</h2>
				<CSSTransitionGroup 
					className="order"
					component="ul"
					transitionName="order"
					transitionEnterTimeout={500}
					transitionLeaveTimeout={500}
					>
					{orderIds.map(this.renderTotal)}
					<li className="total">
						<strong>Total :</strong>
						{formatPrice(total)}
					</li>
				</CSSTransitionGroup>
      </div>
    )
  }
}

Order.propTypes={
	fishes:React.PropTypes.object.isRequired,
	order:React.PropTypes.object.isRequired,
	removeOrder:React.PropTypes.func.isRequired
}

export default Order;




